.characteristics-main{
    display: flex;
    flex-direction: column;
    grid-gap: 22px;
    padding-left: 19px;
    
    &__title{
        color: var(--text, #1D252D);
        font-size: 18px;
        font-family: Gotham;
        font-weight: 500;
        letter-spacing: 0.72px;
        text-transform: uppercase;
    }
    &__subtitle{
        padding-top: 10px;
        color: var(--text, #1D252D);
        font-size: 16px;
        font-family: Gotham;
        font-weight: 500;
        letter-spacing: 0.64px;
        text-transform: uppercase;
    }
}